<template>
    <div class="tagging">
        <navigate title="我的标注" position="absolute"/>
        <div class="scroll-wrapper" ref="scrollWrapper">
            <mu-load-more v-if="list.length" :refreshing="refresh" :loading="loading"
                          @refresh="onRefresh"
                          @load="onLoad">
                <swipe-cell v-for="(item, index) in list" :key="item.id" :right-width="120">
                    <mu-ripple class="item border-1px-b" @click="handlerItem(item)">
                        <mu-avatar size="30" :color="item.shared   ? '#ff9800':'#2196f3'">
                            <img style="padding: 8px;" :src="icon">
                        </mu-avatar>
                        <div class="tagging-content">
                            <div class="title">{{item.title}}</div>
                            <div class="sub">{{item.detail}}</div>
                        </div>
                        <div class="action">{{item.shared   ? '共享':'私有'}}</div>
                    </mu-ripple>
                    <div class="form-item-buttons" slot="right">
                        <mu-button class="button save" @click="handlerSave(item, index)" flat>修改</mu-button>
                        <mu-button class="button delete" @click="handlerDelete(item, index)" flat>删除</mu-button>
                    </div>
                </swipe-cell>
            </mu-load-more>
            <no-success text="您还没有添加标注" v-else/>
        </div>
        <transition name="fade-left">
            <keep-alive>
                <router-view class="views" @update="onRefresh"/>
            </keep-alive>
        </transition>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import NoSuccess from "../../../components/no-success/no-success"
    import SwipeCell from 'vant/lib/swipe-cell'
    import 'vant/lib/swipe-cell/style'
    import {queryILabel, deleteILabelInfo} from "../../../api/personal"
    import {MapMixins} from "../../../common/js/mixins"
    import {isArray} from "../../../types"

    export default {
        name: "tagging",
        mixins: [MapMixins],
        data() {
            return {
                icon: 'static/icon/search_label.png',
                refresh: false,
                loading: false,
                params: {
                    userId: "111",
                    pageNumber: 0,
                    pageSize: 10
                },
                list: []
            }
        },
        activated() {
            this.queryILabel()
        },
        methods: {
            // 点击选项
            handlerItem(item) {
                this.$toast({
                    message: '定位中 ...',
                    duration: 3000,
                    forbidClick: true
                })
                this.setCenter(item.longitude, item.latitude).then(res => {
                    this.$toast('定位成功')

                    this.$router.push('/')
                }).catch(err => {
                    this.$toast('定位失败')
                })
            },
            // 下拉刷新
            onRefresh() {
                this.refresh = true
                this.$refs.scrollWrapper.scrollTop = 0
                this.queryILabel()
            },
            // 上拉加载
            onLoad() {
                this.loading = true
                this.params.pageNumber++

                queryILabel(this.params).then(res => {
                    this.list.push(...res)
                    this.loading = false
                })
            },
            // 搜索
            queryILabel() {
                this.params.pageNumber = 0

                queryILabel(this.params).then(res => {
                    this.list = isArray(res)
                    this.refresh = false
                })
            },
            // 删除
            handlerDelete(item, index) {
                deleteILabelInfo(item.id).then(res => {
                    this.list.splice(index, 1)

                    window.Arcgis && window.Arcgis.refreshMapLayers()
                })
            },
            /***
             * 点击进入修改标注信息界面
             * @param item
             * @param index
             */
            handlerSave(item, index) {
                this.$router.push({
                    name: 'about-tagging-editor',
                    params: {
                        id: item.id
                    }
                })
            }
        },
        components: {
            NoSuccess,
            Navigate,
            SwipeCell
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../../common/stylus/variable.styl"
    @import "../../../common/stylus/mixins.styl"

    .tagging
        padding-top 46px
        background-color #fff
        .scroll-wrapper
            width 100%
            height 100%
            .item
                position relative
                display flex
                align-items center
                padding 0 15px
                height 60px
                .tagging-content
                    flex 1
                    margin-left 10px
                    margin-right 15px
                    overflow hidden
                    .title
                        height 1.5em
                        line-height 1.5
                        color #2D2D2D
                        font-size $font-size-ls
                    .sub
                        color #AAAAAA
                        font-size $font-size-ms
                        height 1.5em
                        line-height 1.5
                        no-wrap()
                .action
                    align-self flex-start
                    margin-top 15px
                    color #aaa
                    font-size $font-size-ms
            .form-item-buttons
                display flex
                align-items center
                width 100%
                height 100%
                .button
                    min-width auto
                    height 100%
                    color #fff
                    border-radius 0
                    &.save
                        background-color #3f51b5
                    &.delete
                        background-color #f44336
</style>



